.header {
    background-color: var(--color-grey);
    position: fixed;
    top: 0;
    width: 100%;
    max-width: var(--desktop);
    z-index: 3;
    
    &::after {
        content: "";
        background-color: var(--color-blue);
        position: absolute;
        bottom: 0px;
        left: 0px;
        width: 100%;
        height: 6px;
        z-index: 3;
    }

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 50px;

        @media screen and (max-width: 1024px) {
            padding: 10px 25px;
        }

        @media screen and (max-width: 799px) {
            padding: 15px 25px;
        }
    }

    .logo {
        display: flex;
        align-items: center;
        text-decoration: none;

        &__text {
            color: var(--color-blue);
            font-family: var(--font-logo);
            font-size: var(--default-font-title);
            text-transform: uppercase;
            width: 100%;

            @media screen and (max-width: 1024px) {
                font-size: var(--default-font-subtitle);
            }
        }

        &-img {
            width: 60px;
            height: 60px;
            object-fit: cover;
        }
    }
}